import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import React from 'react';

import { Friends, Account, Users } from './api';

import Home from './panels/Home';
import VueFriends from './panels/VueFriends';
import VueSubscribers from './panels/VueSubscribers';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			access_token: '10e034eddee1017397f6fb967350f1daf7621e9f3387c100763dbd90ec76dff932db8d2c5732c3b9f33bc',
			friendsIds: null,
			usersInfo: [],
			subscribersInfo: [],
			countSubscribers: 0
		};
	}

	componentDidMount() {
		this.getFriends();
		this.getUserInfo();
		this.getSubscribers();
	}
	getSubscribers () {
		let countSubscribersInterval = 1;
		let offsetSubscribersInterval = 0;
		this.subscribersInterval = setInterval(() => {
			const isClearSubscribersInterval = this.state.countSubscribers - offsetSubscribersInterval < 0;
			if(isClearSubscribersInterval && countSubscribersInterval !== 1) clearInterval(this.subscribersInterval);

			Friends.getRequests({
				count: 100,
				offset: offsetSubscribersInterval,
				out: 1,
				access_token: this.state.access_token
			})
			.then(data => {
				debugger
				offsetSubscribersInterval += 100;
				this.setState({countSubscribers: data.response.count});
				this.getUsersInfo(data.response.items)
					.then(data => {
						this.setState((state) => {
							state.subscribersInfo.push(...data.response);
						});
					})
					.catch(e => console.error(e));
			})
			.catch(e => console.error(e));
		}, 3000);
		
	}
	getFriends() {
		Friends.getFriends({
			count: 5000,
			access_token: this.state.access_token
		})
		.then(data => {
			this.setState({ friendsIds: data.response.items });
			this.getUsersInfo(data.response.items)
				.then(data => {
					this.setState((state) => {
						state.usersInfo.push(...data.response);
					});
				})
				.catch(e => console.error(e));
		})
		.catch(e => console.error(e));
	}
	getUserInfo () {
		Users.getUsers({
			fields: 'photo_200,status,city',
			access_token: this.state.access_token
		})
		.then(data => {
			this.setState({ fetchedUser: data.response[0] });
		})
		.catch(e => console.error(e));
	}
	getUsersInfo (ids) {
		const strFriendsIds = ids.slice(0, 100).join(',');
		return Users.getUsers({
			user_ids: strFriendsIds,
			fields: 'photo_200_orig,status',
			access_token: this.state.access_token
		});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to });
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} access_token={this.state.access_token} />
				<VueFriends
					id="friends"
					go={this.go}
					access_token={this.state.access_token}
					usersInfo={this.state.usersInfo}
				/>
				<VueSubscribers
					id="subscribers"
					go={this.go}
					access_token={this.state.access_token}
					usersInfo={this.state.subscribersInfo}
				/>
			</View>
		);
	}
}

export default App;
